import { Component } from '@angular/core';

@Component({
  selector: 'app-fontdialog',
  templateUrl: './fontdialog.component.html',
  styleUrls: ['./fontdialog.component.css']
})
export class FontdialogComponent  {
  isOpen=false;
  selectedFont='';
  fonts=[
    { name: "verdana" },
    { name: "courier" },
    { name: "Comic Sans MS" },
    { name: "Arial Black" },
    { name: "Times New Roman" },
    { name: "Georgia" },
    { name: "Blackadder ITC" }
  ];

}



